___CSS_LOADER_EXPORT___.push([module.id, `:root{
  --content-spacing: 4px;
  --line-heght: 1.5;
}

.takeABreak__container{
  position : fixed;
  top : 0;
  left : 0;
  width : 100%;
  height : 100%;
  background-color : rgba(0, 0, 0, 0.5);
  z-index : 9999;
  display : flex;
  flex-direction : column;
  justify-content : center;
  align-items : center;
  font-family: Arial, Helvetica, sans-serif;
}

.takeABreak__container > .takeABreak__title{
    color : #fff;
    font-size : 80px;
    font-weight: 600;
    margin: var(--content-spacing);
    line-height: var(--line-heght);
    text-align : center;
    font-family: Arial, Helvetica, sans-serif;
}

.takeABreak__container > .takeABreak__timer{
    color : #fff;
    font-size : 32px;
    margin: var(--content-spacing);
    line-height: var(--line-heght);
    font-weight: 500;
    text-align : center;
    font-family: Arial, Helvetica, sans-serif;
}

.takeABreak__container > .takeABreak__task{
    color : #fff;
    font-size : 32px;
    margin: var(--content-spacing);
    line-height: var(--line-heght);
    text-align : center;
    font-weight: 400;
    font-family: Arial, Helvetica, sans-serif;
}

.takeABreak__container > .takeABreak__dismiss{
  color: #fff;
  padding: 8px;
  background-color: rgb(212, 127, 0);
  border-radius: 4px;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.takeABreak__container > .takeABreak__dismiss:hover{
  background-color: rgb(255, 153, 0);
}
